import React, { FC } from 'react';
import styles from '../stake_table/stake_table.module.scss';
import s from './deposits.module.scss';
import cn from 'classnames';

import { useTranslation } from 'react-i18next';

interface Props {}

const fakeData = [
  {
    pool: 'Pool 1',
    art: 'Anteile gekauft',
    mndcc: '120,0023',
    value: '180,88 $',
    date: new Date(Date.now()),
    color: true,
  },
  {
    pool: 'Pool 1',
    art: 'Stake Auszahlung',
    mndcc: '120,0023',
    value: '180,88 $',
    date: new Date(Date.now()),
    color: false,
  },
  {
    pool: 'Pool 1',
    art: 'Reward Auszahlung',
    mndcc: '120,0023',
    value: '180,88 $',
    date: new Date(Date.now()),
    color: false,
  },
  {
    pool: 'Pool 1',
    art: 'Anteile gekauft',
    mndcc: '120,0023',
    value: '180,88 $',
    date: new Date(Date.now()),
    color: true,
  },
];

export const Deposits: FC<Props> = () => {
  const { t } = useTranslation();

  const timeConverter = (data: Date) => {
    let date = new Date(data);
    let day = date.getDay();
    let month = date.getMonth() + 1;
    let year = date.getFullYear();
    let hours = date.getHours();
    let minutes = date.getMinutes();

    let fixedDay = day < 10 ? '0' + day : day;
    let fixedMonth = month < 10 ? '0' + month : month;
    let fixedHours = hours < 10 ? '0' + hours : hours;
    let fixedMinutes = minutes < 10 ? '0' + minutes : minutes;

    return fixedDay + '.' + fixedMonth + '.' + year + ' ' + fixedHours + ':' + fixedMinutes + ' UTC';
  };
  return (
    <section className={styles.wrapper}>
      <div className={styles.dataBlock}>
        <div className={styles.dataWrapper}>
          <p className={styles.title}>{t('staking-page.Einzahlungen.name')}</p>
        </div>
      </div>
      <div className={styles.tableTitles}>
        <div className={styles.tableTitle} style={{ width: '23rem' }}>
          <p style={{ marginLeft: '2rem', marginBottom: 0 }}>{t('staking-page.stake-table-titles.Pool')}</p>
        </div>
        <div className={styles.tableTitle} style={{ width: '29rem' }}>
          {t('staking-page.Einzahlungen.art')}
        </div>
        <div className={styles.tableTitle} style={{ width: '35rem' }}>
          {t('staking-page.stake-table-titles.MNDCC')}
        </div>
        <div className={styles.tableTitle} style={{ width: '30rem' }}>
          {t('staking-page.stake-table-titles.Wert')} <span style={{ color: '#9A9A9A' }}>USD</span>
        </div>
        <div className={styles.tableTitle} style={{ width: '29rem', marginRight: 0 }}>
          {t('staking-page.Einzahlungen.data')}
        </div>
      </div>
      {fakeData.map((row, i) => (
        <div
          className={cn(styles.dataContainer, { [s.red]: !row.color }, { [s.green]: row.color })}
          style={{ marginTop: '1rem' }}
          key={i}
        >
          <div className={styles.tableTitle} style={{ width: '26rem' }}>
            {row.pool}
          </div>
          <div className={styles.tableTitle} style={{ width: '34rem' }}>
            {row.art}
          </div>
          <div className={styles.tableTitle} style={{ width: '40rem' }}>
            {row.mndcc}
          </div>
          <div className={styles.tableTitle} style={{ width: '35rem' }}>
            {row.value}
          </div>
          <div className={styles.tableTitle} style={{ width: '25rem' }}>
            {timeConverter(row.date)}
          </div>
        </div>
      ))}
    </section>
  );
};
