import React, { useEffect } from 'react';
import { contract } from '../../web3/contract-api';
import { useDispatch, useSelector } from 'react-redux';
import { Layout } from '../layout/layout_parts/layout';
import { ComingSoon } from './coming_soon/coming_soon';
import styles from './home_page.module.scss';
import { LPInformation } from './lp_information/lp_information';
import { LPInfoStaking } from './lp_information_staking/lp_information_staking';
import { PoolDashboardCard } from './lp_pool_dashboard_card/lp_pool_dashboard_card';
import { get_contract_thunk } from '../../redux/actions/actions';
import { RootState } from '../../redux/store';
import Line from '../../assets/images/Path_772.svg';

export const HomePage = () => {
  const dispatch = useDispatch();

  const data = useSelector((state: RootState | any) => state.current_contract.current_contract);

  if (typeof data.contract !== 'undefined') {
    data.contract.claim().then(
      (res: any) => console.log(res),
      (error: { message: any }) => console.log(error.message),
    );
  }

  useEffect(() => {
    contract.connect().then(
      res => dispatch(get_contract_thunk(res)),
      error => console.log(error.message),
    );
  }, [dispatch]);

  return (
    <Layout>
      <section className={styles.wrapper}>
        <img src={Line} className={styles.line} alt="line" />
        <LPInformation />
        <LPInfoStaking mobile />
        <LPInfoStaking />

        <PoolDashboardCard />
        <LPInfoStaking mining revert />
        <ComingSoon />
      </section>
    </Layout>
  );
};
