import React, { FC, useState } from 'react';
import { PoolDashboardCard } from '../home_page/lp_pool_dashboard_card/lp_pool_dashboard_card';
import { Layout } from '../layout/layout_parts/layout';
import { PopUp } from '../pop_up/pop_up';
import { Deposits } from './deposits/deposits';
import { StakeTable } from './stake_table/stake_table';
import styles from './staking_page.module.scss';

export const StakingPage: FC = () => {
  const [isPopUpOpen, setPopUpState] = useState<boolean>(false);
  const popUpStateHandler = () => {
    setPopUpState(!isPopUpOpen);
  };

  return (
    <>
      {isPopUpOpen && (
        <>
          <div className={styles.overlay} onClick={popUpStateHandler}></div>
          <div>
            <PopUp isPopUpOpen={isPopUpOpen} />
          </div>
        </>
      )}
      <Layout>
        <section className={styles.wrapper}>
          <PoolDashboardCard staking_page popUpStateHandler={popUpStateHandler} />
          <StakeTable />
          <Deposits />
        </section>
      </Layout>
    </>
  );
};
